export function hasAllUniqueCharactersDummy(tested: string): boolean {
    let testedArray = tested.split('');
    let uniqueCharacters = [];

    for(let character of testedArray) {
        if (uniqueCharacters.indexOf(character) !== -1) {
            console.log("hasAllUniqueCharacters: doublon is", character, "found at", uniqueCharacters.indexOf(character));
            return false;
        }
        uniqueCharacters.push(character);
    }

    return true;
}

export function hasAllUniqueCharacters(tested: string): boolean {
    let testedArray = tested.split('');
    let asciiTable = [];

    for(let index = 0; index < testedArray.length; index++) {
        let asciiIndex = tested.charCodeAt(index);
        if (asciiTable[asciiIndex]) {
            return false;
        }
        asciiTable[asciiIndex] = true;
    }

    return true;
}


